<template>
	<div class="shop">
		<div class="shop-title">
			<div class="text" @click="back()">销售门店</div>
			<div class="user" @click="messageShow()"></div>
			<ul class="user-message" v-show="MessageShow">
				<li><router-link to="/interior">内部人员登录</router-link></li>
				<li><router-link to="/government">政府监管机构登录</router-link></li>
				<li @click="quit()">退出</li>
			</ul>
		</div>
		<div class="map"><div id="dituContent"></div></div>
		<div class="footer">
			<img src="../assets/fen_logo.png" alt="" />
			汾酒集团信息中心
		</div>
	</div>
</template>

<script>
import BMap from 'BMap';
export default {
	name: 'Shop',
	data() {
		return {
			MessageShow: false,
			center: { lng: 0, lat: 0 },
			zoom: 3
		};
	},
	methods: {
		messageShow() {
			if (this.MessageShow == false) {
				this.MessageShow = true;
			} else {
				this.MessageShow = false;
			}
		},
		back() {
			this.$router.go(-1);
		},
		quit() {
			this.$router.push({
				path: `/`
			});
			return false;
		},
		createMap() {
			function initMap() {
				createMap(); //创建地图
				setMapEvent(); //设置地图事件
				addMapControl(); //向地图添加控件
			}

			//创建地图函数：
			function createMap() {
				var map = new BMap.Map('dituContent'); //在百度地图容器中创建一个地图
				var point = new BMap.Point(111.905469, 37.348818); //定义一个中心点坐标
				var marker = new BMap.Marker(point); // 创建标注
				map.addOverlay(marker); // 将标注添加到地图中
				var label = new BMap.Label('汾酒工业园林', { offset: new BMap.Size(20, -10) });
				marker.setLabel(label);
				map.centerAndZoom(point, 16); //设定地图的中心点和坐标并将地图显示在地图容器中
				window.map = map; //将map变量存储在全局
			}

			//地图事件设置函数：
			function setMapEvent() {
				map.enableDragging(); //启用地图拖拽事件，默认启用(可不写)
				map.enableScrollWheelZoom(); //启用地图滚轮放大缩小
				map.enableDoubleClickZoom(); //启用鼠标双击放大，默认启用(可不写)
				map.enableKeyboard(); //启用键盘上下左右键移动地图
			}

			//地图控件添加函数：
			function addMapControl() {
				//向地图中添加缩放控件
				var ctrl_nav = new BMap.NavigationControl({ anchor: BMAP_ANCHOR_TOP_LEFT, type: BMAP_NAVIGATION_CONTROL_ZOOM });
				map.addControl(ctrl_nav);
				//向地图中添加缩略图控件
				var ctrl_ove = new BMap.OverviewMapControl({ anchor: BMAP_ANCHOR_BOTTOM_RIGHT, isOpen: 0 });
				map.addControl(ctrl_ove);
				//向地图中添加比例尺控件
				var ctrl_sca = new BMap.ScaleControl({ anchor: BMAP_ANCHOR_BOTTOM_LEFT });
				map.addControl(ctrl_sca);
			}

			initMap(); //创建和初始化地图
		}
	},
	mounted() {
		this.createMap();
	}
};
</script>

<style scoped="scoped">
.map {
	flex: 1;
	width: 100%;
	padding: 0.4rem;
	box-sizing: border-box;
}
#dituContent {
	width: 100%;
	height: 100%;
	border: 1px solid #ccccc9;
}
.shop {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;
	overflow: hidden;
}
.shop-title {
	width: 100%;
	background-color: #e6001b;
	height: 0.9rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 0.2rem;
	box-sizing: border-box;
	color: #ffffff;
	position: relative;
}
.shop-title .text {
	float: left;
	font-size: 0.32rem;
	padding-left: 0.4rem;
	background: url(../assets/title_bg1.png) no-repeat left center;
	background-size: contain;
}
.shop-title .user {
	width: 0.6rem;
	height: 0.8rem;
	background: url(../assets/login_ico.png) no-repeat center;
	background-size: 100%;
}
.user-message {
	position: absolute;
	right: 0.1rem;
	top: 0.9rem;
	background-color: #e6001b;
	width: 3rem;
	box-shadow: 0px 0px 0.1rem #b72335;
	z-index: 20;
}
.user-message li {
	text-align: center;
	border-top: 1px solid #b72335;
	font-size: 0.32rem;
	text-align: center;
	color: #ffffff;
	height: 0.8rem;
	line-height: 0.8rem;
}
.user-message li a {
	color: #ffffff;
}
.shop-con {
	flex: 1;
	overflow-y: auto;
	width: 100%;
}
.footer {
	width: 100%;
	/* background: url(../assets/bg.jpg) no-repeat bottom;
	background-size: cover; */
	height: auto;
	height: 90px;
	display: flex;
	align-items: center;
	flex-direction: column;
	font-size: 0.24rem;
	color: #e6001b;
	margin-top: 0.3rem;
}
.footer img {
	width: 50px;
	height: auto;
	margin-bottom: 0.2rem;
}
</style>
